/// Module: Specs --------------------------------------------------------------

.geditorial-admin-wrap-metabox.specs {

	@include main-wrap();

	margin-top: 11px;

	ol.geditorial-specs-list {} // FOR JQUERY SELECTOR
	ul.geditorial-specs-new {} // FOR JQUERY SELECTOR

	ol {
		list-style: none;
		margin: 0;

		li {
			margin-bottom: 2px;
		}

		.item-head {
			border: 1px solid #dfdfdf;
			background-color: #f1f1f1;

			span.item-handle {
				margin: 2px;
				cursor: pointer;
			}

			span.item-excerpt {
				font-size: 0.89em;
				vertical-align: top;
				padding: 3px 5px 0;
				line-height: 16px;
				display: inline-block;
				cursor: pointer;
			}

			span.item-delete {

				float: right;
				margin: 2px;
				cursor: pointer;

				@include body-rtl() {
					float: left;
				}
			}
		}

		.item-body {

			padding: 5px 0;
			display: none;

			border: 1px solid #dfdfdf;
			background-color: #f9f9f9;
			padding: 2px;
			border-top: none;

			.field-wrap-group {
				margin: 3px 3px 2px 3px;

				.field-wrap {

				}

				.field-wrap-textarea {
					margin-bottom: 0px;
				}
			}
		}
	}

	ul {
		.dashicons,
		.dashicons-before:before,
		.field-wrap-textarea,
		.field-wrap-inputtext
		{
			display: none;
		}
	}
}

body.dragging,
body.dragging * {
	cursor: move !important;
}

.dragged {
	position: absolute;
	top: 0;
	opacity: .7;
	z-index: 2000;
	@include body-rtl() {
		right: 0;
	}
}
